.cart-menu{
    position: absolute;
    top: 30px;
    left: -300px;
    background-color: rgb(36, 37, 41);
    width: 400px;
    padding: 10px 20px;
    z-index: 2;
    border-radius: 15px;
}

.cart-menu__arrage{
    margin-top: 20px;
    padding: 20px;
    border-top: 1px rgba(255, 255, 255, 0.4);
}

.cart-menu__total-price{
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
}

@media (max-width: 430px) {
    .cart-menu{
        width: 380px;
        top: 40px;
        left: -222px;
    }
}

@media (max-width: 376px) {
    .cart-menu{
        width: 350px;
        top: 40px;
        left: -192px;
    }
}